@page {
  margin: 0.5cm;
}

@font-face {
  font-family: "PT-Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/PT_Sans-Web-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "PT-Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/PT_Sans-Web-Bold.ttf") format("truetype");
}

body {
  font-family: "PT-Sans", sans-serif;
  font-size: 14px;
}

a {
  text-decoration: none;
  color: #0a1f1f;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.print-header {
  position: relative;
  background-color: #e5e5e5 !important;
  -webkit-print-color-adjust: exact;
  padding: 14px 106px;
  margin-bottom: 31px;
}

.print-header img {
  position: absolute;
  opacity: 0.5;
  top: 10px;
}

.print-header img:nth-child(1) {
  left: 23px;
  width: 22px;
  height: 23px;
}

.print-header img:nth-child(2) {
  right: 23px;
  height: 26px;
  width: 19px;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.coupon {
  border: 1px solid #b2b2b2;
  margin-bottom: 28px;
  padding: 0 25px;
  display: block;
  page-break-inside: avoid;
}

.coupon header {
  text-align: center;
  padding: 17px 0;
  border-bottom: 1px solid #c4c4c4;
}

.coupon header img {
  max-width: 129px;
  display: inline-block;
  margin-right: 29px;
  vertical-align: middle;
}

.coupon header img:last-child {
  margin-right: 0;
}

.coupon footer {
  text-align: center;
  padding: 12px 0;
  border-top: 1px solid #c4c4c4;
}

.coupon__body {
  text-align: center;
  padding: 21px 60px 14px;
}

.coupon__body h1,
.coupon__body h2,
.coupon__body h3,
.coupon__body h4 {
  margin: 0;
  line-height: 1;
  font-weight: 400;
}

.coupon__body h1 {
  font-size: 36px;
  text-transform: uppercase;
  margin-bottom: 14px;
}

.coupon__body h2 {
  font-size: 30px;
  text-transform: uppercase;
  line-height: 1.2;
  margin-bottom: 7px;
}

.coupon__body h3 {
  font-size: 18px;
  margin-bottom: 7px;
}

.coupon__body h4 {
  font-size: 15px;
}
