@page{
  margin: 0.5cm;
}

@font-face{
  font-family:"PT-Sans";
  font-style:normal;
  font-weight:400;
  src:url("../fonts/PT_Sans-Web-Regular.ttf")
  format("truetype");
}
@font-face{
  font-family:"PT-Sans";
  font-style:normal;
  font-weight:700;
  src:url("../fonts/PT_Sans-Web-Bold.ttf")
  format("truetype");
}

body{
  font-family: "PT-Sans", sans-serif;
  font-size: 14px;
}

a{
  text-decoration: none;
  color: #0a1f1f;
}

*{
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

